import inputCell from '@/components/input-cell'
import { changepwd } from '@/api'
export default {
	name: '',
	data () {
		return {
			oldPwd: '',
			newPwd: '',
			cPwd: ''
		}
	},
	onLoad () {

	},
	methods: {
		async onConfirmClick () {
			if(this.oldPwd == '') {
				uni.showToast({
					title: '请输入原密码',
					icon: 'none'
				})
				return;
			}
			if(this.newPwd == '') {
				uni.showToast({
					title: '请输入新密码',
					icon: 'none'
				})
				return;
			}
			let pwdReg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/g;
			if(!pwdReg.test(this.newPwd)) {
				uni.showToast({
					title: '密码必须是6-30位字母与数字的组合',
					icon: 'none'
				})
				return;
			}
			if(this.rpwd == '') {
				uni.showToast({
					title: '请再次输入密码',
					icon: 'none'
				})
				return;
			}
			if(this.cPwd == '') {
				uni.showToast({
					title: '请确认新密码',
					icon: 'none'
				})
				return;
			}
			if(this.cPwd != this.newPwd) {
				uni.showToast({
					title: '两次新密码不一致',
					icon: 'none'
				})
				return;
			}
			let result = await changepwd ({
				token: uni.getStorageSync('token'),
				newpassword: this.newPwd,
				oldpassword: this.oldPwd
			})
			if(result.msg == '重置密码成功') {
				uni.showModal({
					title: '修改密码',
					content: '修改成功',
					showCancel: false,
					success: function (res) {
						if(res.confirm) {
							uni.navigateBack()
						}
					}
				})
			}
		}
	},
	components: {
		inputCell
	}
}
